.timeline
{
    background-color: #21192B;
    color: white;
    font-family: "Metropolis" !important;
    position: relative;
    padding: 0vw 0vw 3vw 0vw;
}
.timeline-title
{
    text-align: center;
    font-family: "Metropolis";
    letter-spacing: 3px;
    font-weight: 700;
    font-size: 2.5vw !important;
}
.timeline-container
{
    position: relative;
    margin: 1vw 100px;
}
.timeline::after
{
    background-image: linear-gradient(#7006AF,#AD201A,#C6332B,#B5065D,#FF5148,#AD201A);
    content: ' ';
    position: absolute;
    top: 0px;
    left: 23rem;
    width: 5px;
    height: 100%;
}
/* .timeline::before
{
    content: '';
    position: absolute;
    top: 0px;
    width: 100%;
    height: 2vw;
    background-color: red;
    clip-path: polygon();
} */
.timeline-event-info
{
    width: 35vw;
    margin: 4vw 0vw 0.5vw 200px;
    position: relative;
}
.timeline-event-info .event-timing
{
    margin-bottom: 1px;
    font-size: 1.1vw;
}
.timeline-event-info .event
{
    font-size: 1.9vw;
    font-family: "Metropolis";
    font-weight: 700;
    margin-top: 1px;
    color :#FF5148;
    text-transform: uppercase;
}
.timeline-event-info .event-desc
{
    margin-top: 1.3vw;
    font-size: 1.1vw;
}
.timeline-event-info .circle
{
    height: 15px;
    width: 15px;
    background: #21192B;
    z-index: 10;
    border: 3px solid #FF5148;
    border-radius: 50%;
    position: absolute;
    top: 32px;
    left: -7.5rem;
}
.timeline-event-info .doodle
{
    position: absolute;
    left: -250px;
    top: 0px;
    height: 9vw;
    width: 9vw;
}
/* .wave
{
