    top: 0px; 
} */
.view-more
{
    background: transparent;
    border: none;
    color: white;
    font-family: "Metropolis";
    font-weight: 800;
    letter-spacing: 2px;
    font-size:1.2vw;
    margin-top: 2vw;
    margin-left: 200px;
}
.aiseHi
{
    height: 20vw;
    background-color: red;
    width: 100%;
}

.fancy-curve {
  position: absolute;
  border-radius: 50%;
  right: 0;
  top: 0;
  border: 25px solid #ff5148;
  max-width: 300px;
  width: 100%;
  max-height: 300px;
  height: 100%;
  transform: translate(40%, -65%);
}
.fancy-circle {
  right: 0;
  top: 0;
  position: absolute;
  max-width: 210px;
  max-height: 210px;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: #c32b24;
  transform: translate(37%, -72%);
  z-index: 10;
}
.aiseHi {
  height: 20vw;
  background-color: red;
  width: 100%;
}
